  [k.SPACETITLE]: "Weltall",
  [k.SPACETEXT]: "Mit der fortschreitenden Erkundung und Nutzung des Weltalls, schreitet auch die Menge an von Menschen geschaffenem Weltraummüll voran. Zum aktuellen Zeitpunkt befinden sich bereits über <strong>6 Tonnen Müll</strong> über der Erdatmosphäre.<br><br> Das Problem darin besteht in der Geschwindigkeit der einzigen Partikel, wodurch bereits kleinste Objekte eine <strong>verheerende Wirkung</strong> bei einem Aufschlag auf beispielsweise einen Satelliten verursachen.<br><br> Zudem kommt, dass ein Zusammenstoß eine <strong>Kettenreaktion</strong> hervorrufen könnte, welche zu einer großen Anzahl an fortführenden Kollisionen führt.",
  [k.OZONETITLE]: "Ozon Schicht",
  [k.OZONETEXT]: "Die Ozonschicht sorgt in der Stratosphäre dafür, dass ein Großteil der <strong>ultravioletten Sonnenstrahlung</strong> absorbiert wird und hat nur indirekt etwas mit dem Klimawandel zu tun, auch wenn diese Begriffe oft miteinander verwendet werden.<br><br> Das erste Ozonloch wurde <strong>1960 über der Antarktis entdeckt.</strong> Auch über Australien und Neuseeland befindet sich ein Ozonloch, welches zu einer hohen Anfälligkeit für Erkrankungen wie Hautkrebs führt.<br><br> Als Gegenmaßnahme wurde <strong>1987 das FCKW-Verbot eingeführt.</strong> Laut einem Bericht der Weltorganisation für Meteorologie besteht die Möglichkeit, dass sich die Ozonschicht bis 2050 weitgehend erholt.",
  [k.AIRTITLE]: "Flugverkehr",
